/*
 *  Featured Content 3
 *    Styles for the featured content 3 section.
 *
 *  Version:
 *    1.0.0 - 2021/11/11
 */

.c-featured-content-3 {
  background-color: $s-color--background-subdued;

  &__outer {
    max-width: $s-page-wrap--xlarge;
    margin: 0 auto;
  }

  &__inner {
    padding: 0 $s-gutter--edge-mobile 2.375rem;
  }

  &__image-wrapper {
    position: relative;
    padding-bottom: 16%;

    &.has-1-image{
      padding-bottom: 0;
    }
  }

  &__image-1-container {
    padding-bottom: 89.5%;
    z-index: $s-z--text-overlay;
    width: 69.69%;
    margin-left: auto;
  }

  &__image-2-container {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: $s-z--illustration-overlay;
    width: 41.26%;
    padding-bottom: 41.26%;
  }

  &__illustration-background {
    position: absolute;
    top: 21%;
    left: 14%;
    z-index: $s-z--reset;
    width: 54.35%;
  }

  &__title {
    margin-top: 2.8125rem;
    margin-bottom: 1.4375rem;
  }

  &__desc {
    margin-left: 4.5625rem;
    max-width: 25.625rem;
  }

  /*** Media Queries ***/

  @include t-media-query($s-small-up) {

    &__inner {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      position: relative;
    }

    &__image-wrapper {
      width: 56%;
    }

    &__content {
      position: absolute;
      width: 39%;
      left: $s-gutter--edge-mobile;
      z-index: $s-z--illustration-overlay;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      display: flex;
      flex-direction: row-reverse;
      position: relative;
      padding: 6.6875rem $s-gutter--edge-medium 6.25rem;
    }

    &__content {
      left: $s-gutter--edge-medium;
    }

    &__image-wrapper {
      width: 59%;
    }

    &__image-1-container {
      padding-bottom: 88.5%;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      padding: 6.6875rem $s-gutter--edge-large 9.4375rem;
    }

    &__image-wrapper {
      width: 70%;
      padding-bottom: 10.2%;
    }

    &__image-2-container {
      width: 40.26%;
      padding-bottom: 40.26%;
      bottom: -0.25rem;
      left: 0.625rem;
    }

    &__content {
      left: $s-gutter--edge-large;
    }

    &__illustration-background {
      top: 20%;
      left: 15%;
      width: 56.35%;
    }

    &__title{
      margin-top: 8.8125rem;
      margin-bottom: 2.5rem;
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__desc{
      margin-left: 6.9375rem;
    }
  }
}